.transactions {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.transactions .summary-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.transactions .summary-cards-row .summary-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  position: relative;
}
.transactions .summary-cards-row .summary-card .card-content .card-value {
  font-size: 1.875rem;
  font-weight: 700;
  color: #111827;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.transactions .summary-cards-row .summary-card .card-content .card-value.success {
  color: #10b981;
}
.transactions .summary-cards-row .summary-card .card-content .card-value.warning {
  color: #f59e0b;
}
.transactions .summary-cards-row .summary-card .card-content .card-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.transactions .summary-cards-row .summary-card .card-content .trend-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  color: #9ca3af;
}
.transactions .summary-cards-row .summary-card.total-transactions .card-value {
  color: #111827;
}
.transactions .summary-cards-row .summary-card.receipts .trend-icon {
  color: #10b981;
}
.transactions .summary-cards-row .summary-card.issues .trend-icon {
  color: #f59e0b;
}
.transactions .summary-cards-row .summary-card.adjustments .trend-icon {
  color: #6b7280;
}
.transactions .search-filter-bar {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  margin-bottom: 2rem;
  display: flex;
  gap: 1rem;
  align-items: center;
}
.transactions .search-filter-bar .search-container {
  flex: 1;
  position: relative;
}
.transactions .search-filter-bar .search-container .search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
}
.transactions .search-filter-bar .search-container .search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: #f9fafb;
}
.transactions .search-filter-bar .search-container .search-input:focus {
  outline: none;
  border-color: #3b82f6;
  background-color: white;
}
.transactions .search-filter-bar .search-container .search-input::placeholder {
  color: #9ca3af;
}
.transactions .search-filter-bar .filter-dropdown {
  position: relative;
  min-width: 150px;
}
.transactions .search-filter-bar .filter-dropdown .filter-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  appearance: none;
  cursor: pointer;
}
.transactions .search-filter-bar .filter-dropdown .filter-select:focus {
  outline: none;
  border-color: #3b82f6;
}
.transactions .search-filter-bar .filter-dropdown .dropdown-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.transactions .transactions-table-container {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
}
.transactions .transactions-table-container .transactions-table {
  width: 100%;
  border-collapse: collapse;
}
.transactions .transactions-table-container .transactions-table thead {
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}
.transactions .transactions-table-container .transactions-table thead th {
  padding: 1rem;
  text-align: left;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  border-bottom: 1px solid #e5e7eb;
}
.transactions .transactions-table-container .transactions-table tbody tr {
  border-bottom: 1px solid #f3f4f6;
}
.transactions .transactions-table-container .transactions-table tbody tr:last-child {
  border-bottom: none;
}
.transactions .transactions-table-container .transactions-table tbody tr:hover {
  background-color: #f9fafb;
}
.transactions .transactions-table-container .transactions-table tbody tr td {
  padding: 1rem;
  font-size: 0.875rem;
  color: #111827;
  vertical-align: top;
}
.transactions .transactions-table-container .transactions-table tbody tr td.datetime {
  font-weight: 500;
  color: #374151;
}
.transactions .transactions-table-container .transactions-table tbody tr td.part .part-primary {
  font-weight: 600;
  color: #3b82f6;
  margin-bottom: 0.25rem;
}
.transactions .transactions-table-container .transactions-table tbody tr td.part .part-secondary {
  font-size: 0.75rem;
  color: #6b7280;
}
.transactions .transactions-table-container .transactions-table tbody tr td.location {
  font-weight: 500;
}
.transactions .transactions-table-container .transactions-table tbody tr td.type .type-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
}
.transactions .transactions-table-container .transactions-table tbody tr td.type .type-badge.adjustment {
  background-color: #f3f4f6;
  color: #374151;
}
.transactions .transactions-table-container .transactions-table tbody tr td.type .type-badge.receipt {
  background-color: #d1fae5;
  color: #065f46;
}
.transactions .transactions-table-container .transactions-table tbody tr td.type .type-badge.issue {
  background-color: #fee2e2;
  color: #991b1b;
}
.transactions .transactions-table-container .transactions-table tbody tr td.type .type-badge.transfer {
  background-color: #dbeafe;
  color: #1e40af;
}
.transactions .transactions-table-container .transactions-table tbody tr td.quantity {
  font-weight: 600;
  text-align: center;
}
.transactions .transactions-table-container .transactions-table tbody tr td.quantity.positive {
  color: #10b981;
}
.transactions .transactions-table-container .transactions-table tbody tr td.quantity.negative {
  color: #ef4444;
}
.transactions .transactions-table-container .transactions-table tbody tr td.unit-cost, .transactions .transactions-table-container .transactions-table tbody tr td.total-cost {
  color: #6b7280;
  text-align: center;
}
.transactions .transactions-table-container .transactions-table tbody tr td.reference {
  font-weight: 500;
  color: #6b7280;
}
.transactions .transactions-table-container .transactions-table tbody tr td.reason {
  font-weight: 500;
}
.transactions .transactions-table-container .transactions-table tbody tr td.notes {
  color: #6b7280;
  font-style: italic;
}
.transactions .fab {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.transactions .fab:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.transactions .fab svg {
  color: white;
}

@media (max-width: 1024px) {
  .transactions .search-filter-bar {
    flex-direction: column;
    align-items: stretch;
  }
  .transactions .search-filter-bar .search-container {
    min-width: auto;
  }
  .transactions .search-filter-bar .filter-dropdown {
    min-width: auto;
  }
  .transactions .transactions-table-container {
    overflow-x: auto;
  }
  .transactions .transactions-table-container .transactions-table {
    min-width: 1000px;
  }
}
@media (max-width: 768px) {
  .transactions {
    padding: 1rem;
  }
  .transactions .summary-cards-row {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
  }
  .transactions .summary-cards-row .summary-card {
    padding: 1rem;
  }
  .transactions .summary-cards-row .summary-card .card-content .card-value {
    font-size: 1.5rem;
  }
  .transactions .summary-cards-row .summary-card .card-content .card-label {
    font-size: 0.75rem;
  }
  .transactions .summary-cards-row .summary-card .trend-icon {
    width: 1.25rem;
    height: 1.25rem;
  }
  .transactions .search-filter-bar {
    padding: 1rem;
  }
}
@media (max-width: 480px) {
  .transactions .summary-cards-row {
    grid-template-columns: 1fr;
  }
  .transactions .search-filter-bar .search-container .search-input {
    padding: 0.75rem 1rem 0.75rem 2.5rem;
  }
}
